// components
import { Dictaphone } from './components/Dictaphone/Dictaphone';

// mui-components
import { Box } from '@mui/material';

// sweetalert
import swal from 'sweetalert';

// styles
import './App.css';

export const App = () => {
  if (!localStorage.getItem('Introduced')) {
    swal({
      title: "Info",
      text: `Text to speech and speech to text recognition are partially supported by browsers. The Edge and Chrome browsers have the best support.`,
      icon: "info",
      button: "OK",
    });
    
    localStorage.setItem('Introduced', true);
  }
  
  return (
    <Box className="App">
      <Dictaphone />
    </Box>
  );
};
